import chunLeiModal from '@/components/chunLei-modal/chunLei-modal.vue'
import uniPopup from "@/components/uni-popup/uni-popup.vue"
export default {
	data() {
		return {
			showReadcodeInput: false,
			mData: {
				title: '输入阅读码',
				content: [{
					content: '',
					type: 'text',
					placeholder: '请输入阅读码'
				}, ]
			},
			descData: {
				title: '购买说明',
				content: '1. 购买成功后可直接点击查看阅读期刊内容， 或在“ 我的杂志” 列表查看；\r\n2. 电子刊为虚拟商品， 如无系统问题， 购买后不可退换；\r\n3. 每次购买成功后， 阅读码将显示在阅读码列表中；\r\n4. 一个阅读码只可以被激活一次， 未激活的阅读码可赠与好友；\r\n5. 如有其他使用问题可私信官方账号获取支持；',
				cancelText: 'cancel'
			},
			buyTypes: [{
				num: 1,
				price: 8
			}, {
				num: 10,
				price: 70
			}, {
				num: 50,
				price: 350
			}, {
				num: 100,
				price: 600
			}],
			ordinaryNum: 0,
			choosenIndex: 0,
			showDescModal: false,
			isIos: false
		};
	},
	mounted() {
		let that = this;
		uni.getSystemInfo({
			success: function(res) {
				if (res.platform == 'ios') {
					that.isIos = true;
				}
				// uni.showModal({
				//     title: '提示',
				//     content: that.isIos,
				//     success: function (res) {
				//         if (res.confirm) {
				//             console.log('用户点击确定');
				//         } else if (res.cancel) {
				//             console.log('用户点击取消');
				//         }
				//     }
				// });
			}
		})
	},
	methods: {
		onReadcodeInputConfirm(content) {
			if (content[0].content) {
				console.log(content[0].content, '阅读码');
			}
		},
		clickType(index) {
			this.choosenIndex = index;
		},
		clickOrdinary(flag) {
			if (this.choosenIndex !== 4) {
				this.choosenIndex = 4;
				return;
			}
			if (flag) {
				this.ordinaryNum++;
			} else {
				if (this.ordinaryNum == 0) return;
				this.ordinaryNum--;
			}
		},
		showDesc() {
			this.showDescModal = true;
		}
	},
	computed: {
		price: function() {
			if (this.choosenIndex == 4) {
				let single = 0;
				if (this.ordinaryNum < 10) {
					single = 8;
				} else if (this.ordinaryNum >= 10 && this.ordinaryNum <= 50) {
					single = 7;
				} else if (this.ordinaryNum > 50) {
					single = 6;
				}
				return this.ordinaryNum * single;
			} else {
				return this.buyTypes[this.choosenIndex].price;
			}
		}
	},
	components: {
		chunLeiModal,
		uniPopup
	}
}
